.about-me {
    width: 100%;
    height: auto;
    padding: 4rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    background-color: var(--subBackgroundColor);
}

.about-cards {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.about-card {
    width: 200px;
    height: 200px;
    background-color: var(--aboutMeCardColor);
    padding: 8px;
    border-radius: 4px;
    margin: 2rem 12px 12px 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: all 0.3s ease-in-out;
}

.about-card i {
    margin-bottom: 1rem;
}

.about-card:hover {
    background-color: var(--aboutMeHoverColor);
    color: var(--lightTextColor);
    -webkit-box-shadow: 0 0 8px 4px rgba(0, 0, 0, 0.22);
    box-shadow: 0 0 8px 4px rgba(0, 0, 0, 0.22);
    transform: translateY(-20px);
}

.about-title {
    margin-top: 2rem;
    font-size: 2.5rem;
    font-weight: 400;
}