.contact-container {
    width: 100%;
    height: auto;
    padding: 4rem 4rem 2rem 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    background-color: var(--subBackgroundColor);
}

.contacts {
    display: flex;
    flex-direction: row;
}

.contact-icon {
    margin: 2rem;
    cursor: pointer;
    color: var(--aboutMeHoverColor);
}

.contact-icon:hover {
    color: var(--aboutMeCardColor);
}

.copyright {
    color: var(--subTextColor);
}