.skills {
    width: 100%;
    height: auto;
    background-color: var(--backgroundColor);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0;
}

.skill-content {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.skill-table {
    display: flex;
    flex-direction: column;
    margin: 2rem;
}

.skills-title {
    font-size: 2.5rem;
    font-weight: 400;
}

.skill-title {
    color: var(--darkBlueTextColor);
    font-weight: 500;
    margin-bottom: 12px;
}

.skill {
    font-size: 1rem;
    padding: 8px 4px;
    font-weight: bold;
    color: var(--subTextColor);
}