.project-view {
    width: 100%;
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s ease-in-out;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}

.selected-image {
    height: 500px;
    border-radius: 6px;
    border: 4px solid var(--backgroundColor);
}

.sub-images {
    max-width: 80%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    overflow: hidden;
    flex-direction: row;
    background-color: var(--backgroundColor);
    border-radius: 6px;
    margin-top: 2rem;
}

.sub-image {
    max-height: 150px;
    margin: 1rem;
    opacity: 1;
    border-radius: 4px;
    border: 4px solid var(--darkBlueTextColor);
    transition: all 0.25s ease-in-out;
}

.sub-image:hover {
    opacity: 0.7;
}

.projects-container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0;
    background-color: var(--subBackgroundColor);
}

.projects {
    display: grid;
    grid-template-columns: repeat(2, 580px);
    grid-template-rows: repeat(3, 220px);
    grid-gap: 24px 24px;
    margin-top: 2rem;
}

.project {
    width: 580px;
    height: 220px;
    padding: 1.5rem;
    box-sizing: border-box;
    overflow: hidden;
    background-color: var(--backgroundColor);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: box-shadow 0.3s ease, max-height 2.0s ease, padding 2.0s ease, opacity 2.0s ease;
    border-radius: 6px;
}

.project:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.project-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.project-header h2 {
    color: var(--darkBlueTextColor);
}

.project-header i {
    padding: 0 6px;
    color: var(--subTextColor);
}

.project-desc {
    font-size: 22px;
}

@media (min-width: 768px) and (max-width: 1220px){
    .projects {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
    }

    .project {
        width: 45vw;
        height: auto;
        min-height: 220px;
    }
}

@media (max-width: 1024px) {
    .selected-image {
        max-width: 90%;
        height: auto;
    }

    .sub-image {
        max-height: 80px;
    }
}

@media (min-width: 428px) and (max-width: 768px) {
    .projects {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
    }

    .project {
        width: 45vw;
        height: auto;
        min-height: 220px;
    }
}

@media (max-width: 428px) {
    .projects {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .project {
        width: 90%;
        height: auto;
        min-height: 220px;
    }
}

