.certs-content {
    width: 100%;
    height: auto;
    background-color: var(--backgroundColor);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0;
}

.certs-title {
    font-size: 2.5rem;
    font-weight: 400;
}

.certs {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.certification {
    width: 260px;
    min-height: 60px;
    flex-wrap: wrap;
    padding: 1.5rem;
    margin: 1rem;
    border-radius: 6px;
    background-color: var(--subBackgroundColor);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: box-shadow 0.3s ease, max-height 2.0s ease, padding 2.0s ease, opacity 2.0s ease;
}

.certification:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.certification h3 {
    color: var(--darkBlueTextColor);
    margin-bottom: 0.5rem;
}

.certification h5 {
    color: var(--subTextColor);
}

@media (min-width: 428px) and (max-width: 768px) {
    .certification {
        width: 200px;
    }
}


@media (max-width: 428px) {
    .certification {
        width: 90%;
    }
}