.navbar {
    width: 100%;
    min-height: 50px;
    box-sizing: border-box;
    padding: 4px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-bottom: 2px solid var(--navbarBorder);

    background-color: var(--navbarColor);
    position: fixed;
    z-index: 100;
}

.navlink {
    text-decoration: none;
    color: var(--textColor);
    font-size: 20px;
    margin: 0 6px;
}

.hover-effect {
    display: inline-block;
}

.hover-effect:after {
    display:block;
    content: '';
    border-bottom: solid 3px var(--darkBlueTextColor);
    transform: scaleX(0);
    transition: transform 300ms ease-in-out;
}

.hover-effect:hover:after {
    transform: scaleX(1);
}